import React from "react"
import {Link} from "react-router-dom"

export default ({total, tags}) => {
  return <main className="container">
    <header style={{marginBottom: '10px'}}>文章总数: {total}</header>
    {map(tags, 3)}
  </main>
}

/**
 * 将array分组输出
 *
 * @param arr 原始数组
 * @param size 每组元素数
 */
function map(arr, size = 3) {

  return Array.from({length: Math.ceil(arr.length / size)})
    .map((v, i) => arr.slice(i * size, (i + 1) * size))
    .map((page, i) => {
      return <div key={i} className="columns">
        {
          page.map(tagCard)
        }
      </div>
    })
}

function tagCard(tag) {
  const color = '#' + Math.floor(Math.random() * 16777215).toString(16);
  return <div key={tag.key} className="column is-4">
        <span className='tag'
              style={{
                width: '100%',
                background: color,
              }}>
          {tag.key}({tag.doc_count})
        </span>
  </div>
}
